.top-cards-div {


    background: #000;
    padding-bottom: 80px;
    padding-left: 150px;
    height: auto;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url('./../../Assets/bg-image.svg');

    h2 {
        font-size: 46px;
        font-weight: bold;
    }

    h4 {
        text-align: left !important;
        font-weight: bold;
        font-size: 18px;
        padding: 15px
    }

    p{
        text-align: left !important; 
        padding: 15px 25px;
    }

    .innerCards {

        position: relative;
       // width: 580px;
        height: 275px;
        left: 2px;
        top: 0px;

        background: #1B1C1E;
        border: 2px solid #FFFFFF;
        box-sizing: border-box;
        border-radius: 20px;
        // background: #ffffff;
        // box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.05);
        // border-radius: 10px;
        // padding: 40px;
    }

    button {
        border: none;
        outline: none;
        background: transparent;
    }

}

.text-color {
    color: #ffffff;
}

.newheader {
    background: linear-gradient(91.21deg, #FFFFFF 4.18%, rgba(255, 255, 255, 0) 215.81%) !important;
    height: 80px;
    padding: 20px 0;
    border-radius: 17px 17px 0px 0px;
    
}

.pdf-container {
    width: 100%;
    // height: 700px;
    margin-bottom: 60px;
    margin-top: -60px;

    // overflow: scroll;
    // overflow-x: hidden;
    embed {
        border-radius: 13px;
    }
}
.pts{
    padding: 30px 0;
}

.address{
    color: #ffffff;
}

.form-control{
    margin: 3px 50px;
    width: 80%;
    background: #1B1C1E !important;
}
.btn-update{
    background: #000;
    color: #fff;
    margin: 25px 0;
    border: 1px solid #FFFFFF !important;
    width: 80%;
    font-size: 20px !important;
    border-radius: 10px !important;
   // border-radius: 20px;
}

